import { FormGroup } from '@angular/forms';
import { IFranchise } from '@interfaces';
import { Franchise } from '@models';

export class BusinessPayload {
  businessForm: FormGroup;

  constructor(pBusinessForm: FormGroup) {
    this.businessForm = pBusinessForm;
  }

  toJson(): Franchise {
    const form = this.businessForm.value;

    const body: IFranchise = {
      id: form.id || null,
      name: form.name || null,
      price_range: form.price_range,
      style: form.style,
    };

    return body;
  }
}
