import { AmenitiesToSave } from '@models';
import { FormGroup } from '@angular/forms';

export class RestuarantParkingPayload {
  parkingForm: FormGroup;

  constructor(pParkingForm: FormGroup) {
    this.parkingForm = pParkingForm;
  }

  toJson(): AmenitiesToSave {
    return {
      parkings: this.parkingForm.get('parking').value || []
    };
  }
}
