import { FormGroup } from '@angular/forms';
import { Restaurant } from '@models';

export class RestaurantPayload {
  newRestaurantForm: FormGroup;

  constructor(pNewRestaurantForm: FormGroup) {
    this.newRestaurantForm = pNewRestaurantForm;
  }

  public toJson(avoidManagerUpdate?: boolean): Restaurant {
    const form = this.newRestaurantForm;

    const restaurantInfo = {
      name: form.get('name').value,
      description: form.get('description').value,
      coordinates: this.concatCoordinates(),
      address: form.get('address').value,
      phone: this.getPhone(),
      email: form.get('email').value,
      invoice_email: '',
      invoice_url: '',
      manager:
        (form.get('manager').value || form.get('manager').value === 0) &&
        !avoidManagerUpdate
          ? form.get('manager').value
          : undefined,
    };

    if (form.value.billingOption === '0') {
      restaurantInfo.invoice_email = form.value.emailBilling;
      delete restaurantInfo.invoice_url;
    } else {
      restaurantInfo.invoice_url = form.value.websiteBilling;
      delete restaurantInfo.invoice_email;
    }

    return restaurantInfo as Restaurant;
  }

  private getPhone(): string {
    return `+${this.newRestaurantForm.get('dialCode').value}${
      this.newRestaurantForm.get('phone').value
    }`;
  }

  private concatCoordinates(): Array<number> {
    const coordinatesArray: Array<number> = [];

    coordinatesArray.push(
      parseFloat(this.newRestaurantForm.get('latitude').value)
    );
    coordinatesArray.push(
      parseFloat(this.newRestaurantForm.get('longitude').value)
    );

    return coordinatesArray;
  }
}
