import { FormGroup } from '@angular/forms';
export class RestaurantAppDataPayload {
  appDataForm: FormGroup;

  constructor(pAppDataForm: FormGroup) {
    this.appDataForm = pAppDataForm;
  }

  toJson() {
    return {
      max_offered_seats: this.appDataForm.get('max_offered_seats').value,
      max_people_per_order: this.appDataForm.get('max_people_per_order').value,
      max_cancel_limit_minutes: this.getTotalMinutes(),
      reservation_time_minutes: this.appDataForm.get('reservation_time_minutes').value,
    };
  }

  private getTotalMinutes(): number {
    const hoursToMinutes = this.appDataForm.get('max_cancel_limit_hours').value * 60;
    const totalMinutes = +hoursToMinutes + +this.appDataForm.get('max_cancel_limit_minutes').value;

    return totalMinutes;
  }
}
