import { FormGroup } from '@angular/forms';
import { AmenitiesToSave } from '@models';

export class RestuarantReservationZonePayload {
  reservationZonesForm: FormGroup;

  constructor(pReservationZonesFOrm: FormGroup) {
    this.reservationZonesForm = pReservationZonesFOrm;
  }

  toJson(): AmenitiesToSave {
    return {
      zones: this.reservationZonesForm.get('zones').value || []
    };
  }
}
