import { IPaymentMethod } from '@interfaces';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Observable, Subscription } from 'rxjs';
import { RestaurantService } from '@services';
import { RestuarantPaymentMethodPayload } from '@payloads';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, FormBuilder } from '@angular/forms';
import { PaymentMethod, Amenities, ResponseDetail } from '@models';
import { map, mergeMap } from 'rxjs/operators';

@Component({
  selector: 'app-payment-methods',
  templateUrl: './payment-methods.component.html',
  styleUrls: ['./payment-methods.component.scss']
})
export class PaymentMethodsComponent implements OnInit, OnDestroy {
  paymentMethodsForm: FormGroup = this.fb.group({
    paymentMethods: new FormControl([])
  });
  paymentMethods: Array<PaymentMethod> = [];
  currentPaymentMethods: Array<number>;
  restaurantId: number = parseInt(localStorage.getItem('restaurantId'), 10);
  restaurantSubscription = Subscription.EMPTY;
  paymentMethodsSubscription = Subscription.EMPTY;

  constructor(
    private fb: FormBuilder,
    private restaurantService: RestaurantService,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    this.restaurantSubscription = this.getRestaurantDetails(this.restaurantId)
      .pipe(
        mergeMap((response: ResponseDetail): any => {
          this.currentPaymentMethods = response.restaurant.payment_methods
            .filter(
              (pPaymentMethod: IPaymentMethod) => pPaymentMethod.active === true
            )
            .map((pPaymentMethod: IPaymentMethod) => pPaymentMethod.id);

          this.paymentMethodsForm.patchValue({
            paymentMethods: this.currentPaymentMethods
          });

          return this.getPaymentMethods();
        })
      )
      .subscribe((response: any) => {
        this.createPaymentMethodsObjects(response as Array<PaymentMethod>);
      });
  }

  ngOnDestroy() {
    this.restaurantSubscription.unsubscribe();
    this.paymentMethodsSubscription.unsubscribe();
  }

  onPaymentMethodCheck(paymentMethod: IPaymentMethod): void {
    paymentMethod.active = !paymentMethod.active;

    let currentSelectedPaymentMethods = this.paymentMethodsForm.get(
      'paymentMethods'
    ).value;

    currentSelectedPaymentMethods = this.onPaymentMethodSelection(
      paymentMethod,
      currentSelectedPaymentMethods
    );

    this.paymentMethodsForm.patchValue({
      paymentMethods: currentSelectedPaymentMethods
    });
  }

  onSubmit(): void {
    const paymentMethodsPayload = new RestuarantPaymentMethodPayload(
      this.paymentMethodsForm
    );

    this.paymentMethodsSubscription = this.restaurantService
      .setRestaurantAmenities(paymentMethodsPayload.toJson(), this.restaurantId)
      .subscribe(() => this.openSnackBar('Cambios guardados', 'Entendido'));
  }

  private createPaymentMethodsObjects(
    paymentMethods: Array<PaymentMethod>
  ): void {
    const paymentMethodsOptions = paymentMethods;

    paymentMethodsOptions.forEach(pPaymentMethod => {
      const paymentMethodObject = {
        name: pPaymentMethod.name,
        id: pPaymentMethod.id,
        active: false
      };

      const isActive = this.currentPaymentMethods.find(
        id => id === pPaymentMethod.id
      );

      if (isActive) {
        paymentMethodObject.active = true;
      }

      this.paymentMethods.push(paymentMethodObject);
    });
  }

  private getPaymentMethods(): Observable<Array<PaymentMethod>> {
    return this.restaurantService.getAmenities().pipe(
      map((response: Amenities) => {
        const paymentMethods: Array<PaymentMethod> = response.payment_methods.map(
          (pPaymentMethod: IPaymentMethod) => {
            return new PaymentMethod(pPaymentMethod);
          }
        );

        return paymentMethods;
      })
    );
  }

  private getRestaurantDetails(id: number): Observable<ResponseDetail> {
    return this.restaurantService.getRestaurantDetail(id);
  }

  private onPaymentMethodSelection(
    paymentMethod: IPaymentMethod,
    currentPaymentMethods: Array<number>
  ): Array<number> {
    const isPaymentMethodSelected = currentPaymentMethods.includes(
      paymentMethod.id
    );

    if (isPaymentMethodSelected) {
      const index = currentPaymentMethods.indexOf(paymentMethod.id);

      currentPaymentMethods.splice(index, 1);
    } else {
      currentPaymentMethods.push(paymentMethod.id);
    }

    return currentPaymentMethods;
  }

  private openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000
    });
  }
}
