import { FormGroup } from '@angular/forms';
import { AmenitiesToSave } from '@models';

export class RestuarantPaymentMethodPayload {
  paymentMethodsForm: FormGroup;

  constructor(pPaymentMethodsForm: FormGroup) {
    this.paymentMethodsForm = pPaymentMethodsForm;
  }

  toJson(): AmenitiesToSave {
    return {
      payment_methods: this.paymentMethodsForm.get('paymentMethods').value || []
    };
  }
}
