import { FormGroup, FormArray } from '@angular/forms';

export class RestaurantImagesPayload {
  private imagesForm: FormGroup;

  constructor(pImagesForm: FormGroup) {
    this.imagesForm = pImagesForm;
  }

  public toFormData(): FormData {
    const galleryData = new FormData();

    galleryData.append('cover', this.imagesForm.get('coverImage').value);

    const galleryImages = this.imagesForm.get('galleryImages') as FormArray;

    galleryImages.controls.forEach((control, i) => {
      if (!control.get('id')) {
        galleryData.append(
          'gallery[' + i + ']',
          galleryImages.controls[i].get('fileValue').value
        );
      }
    });

    if (this.imagesForm.get('imageLink').value) {
      galleryData.append('matterport', this.imagesForm.get('imageLink').value);
    }
    return galleryData;
  }
}
