import { IWorkingHour } from '@interfaces';
import { WorkingHoursResponse } from '@models';
import { FormGroup, FormArray } from '@angular/forms';

export class RestaurantWorkingHoursPayload {
  private workingHoursForm: FormGroup;

  constructor(pWorkingHoursForm: FormGroup) {
    this.workingHoursForm = pWorkingHoursForm;
  }

  public fixedHoursJson(): WorkingHoursResponse {
    return { working_hours: this.getFixedHours() };
  }

  public mixedHoursJson(): WorkingHoursResponse {
    return { working_hours: this.getMixedHours() };
  }

  private getMixedHours() {
    const currentWeekdays = this.workingHoursForm.get(
      'mixedHours'
    ) as FormArray;
    const activeDays: Array<IWorkingHour> = [];

    currentWeekdays.controls.forEach((weekday, index) => {
      if (weekday.get('isSelected').value === true) {
        const weekdayObject = {
          week_day: index + 1,
          opens_at: weekday.get('opening').value,
          closes_at: weekday.get('closing').value
        };

        activeDays.push(weekdayObject);
      }
    });

    return activeDays;
  }

  private getFixedHours(): Array<IWorkingHour> {
    const hours: Array<IWorkingHour> = [];

    Object.keys(this.workingHoursForm.value.fixedHours.weekdays).forEach(
      day => {
        const fixedHours = this.workingHoursForm.get('fixedHours');
        if (fixedHours.get('weekdays').get(day).value) {
          const object = {
            week_day: parseInt(day, 10),
            opens_at: fixedHours.get('opening').value,
            closes_at: fixedHours.get('closing').value
          };

          hours.push(object);
        }
      }
    );

    return hours;
  }
}
