import { FormGroup } from '@angular/forms';
import { NewBranch } from '@models';

export class RestaurantDetailPayload {
  restaurantDetailForm: FormGroup;

  constructor(pRestaurantDetailForm: FormGroup) {
    this.restaurantDetailForm = pRestaurantDetailForm;
  }

  toJson(): NewBranch {
    return {
      name: this.restaurantDetailForm.get('name').value,
      description: this.restaurantDetailForm.get('description').value,
      coordinates: this.getCoordinatesArray(),
      address: this.restaurantDetailForm.get('address').value,
      address_extra: this.restaurantDetailForm.get('address_extra').value,
      phone: this.getParsedPhone(),
      email: this.restaurantDetailForm.get('email').value
    };
  }

  private getCoordinatesArray(): Array<number> {
    const coordArray = [
      parseFloat(this.restaurantDetailForm.get('lat').value),
      parseFloat(this.restaurantDetailForm.get('long').value)
    ];

    return coordArray;
  }

  private getParsedPhone(): string {
    const form = this.restaurantDetailForm;
    const concatPhone = `+${form.get('countryCode').value} ${form
      .get('phone')
      .value.trim()}`;

    return concatPhone;
  }
}
