import { FormGroup } from '@angular/forms';
import { AmenitiesToSave } from '@models';

export class RestuarantServiceTypePayload {
  serviceTypeForm: FormGroup;

  constructor(pServiceTypeForm: FormGroup) {
    this.serviceTypeForm = pServiceTypeForm;
  }

  toJson(): AmenitiesToSave {
    return {
      types: this.serviceTypeForm.get('type').value || []
    };
  }
}
