import { Extra, AmenitiesToSave } from '@models';
import { FormGroup } from '@angular/forms';
import { IExtra } from '@interfaces';

export class RestuarantExtraPayload {
  extrasForm: FormGroup;

  constructor(pExtrasForm: FormGroup) {
    this.extrasForm = pExtrasForm;
  }

  toJson(): AmenitiesToSave {
    return {
      extras: this.getExtras()
    };
  }

  private getExtras(): Array<IExtra> {
    const extras = this.extrasForm.getRawValue().extras.map((extra: Extra) => {
      if (extra.id) {
        return {
          id: extra.id,
          active: extra.active
        };
      } else {
        return {
          name: extra.name,
          active: extra.active
        };
      }
    });

    return extras;
  }
}
