import { AmenitiesToSave } from '@models';
import { IExtra } from '@interfaces';

export class AmenitiesPayload {
  types?: Array<number>;
  zones?: Array<number>;
  extras?: Array<IExtra>;
  parkings?: Array<number>;
  paymentMethods?: Array<number>;

  constructor(
    pTypes: Array<number>,
    pZones: Array<number>,
    pExtras: Array<IExtra>,
    pParkings: Array<number>,
    pPpaymentMethods: Array<number>
  ) {
    this.types = pTypes;
    this.zones = pZones;
    this.extras = pExtras;
    this.parkings = pParkings;
    this.paymentMethods = pPpaymentMethods;
  }

  toJson(): AmenitiesToSave {
    return {
      types: this.types,
      zones: this.zones,
      extras: this.getExtras(),
      parkings: this.parkings,
      payment_methods: this.paymentMethods
    };
  }

  private getExtras(): Array<IExtra> {
    const extras = this.extras.map((extra: IExtra) => {
      if (extra.id) {
        return {
          id: extra.id,
          active: extra.active
        };
      } else {
        return {
          name: extra.name,
          active: extra.active
        };
      }
    });

    return extras;
  }
}
